.btn-tips {
  position: absolute;
  z-index: 89;
  font-size: 2.3rem;
  left: 0.6rem;
  top: 1.2rem;
  color: white;
  animation: Rotate 2s infinite;
}
@keyframes Rotate {
  0% {
    transform: rotate(0);
  }
  33% {
    transform: rotate(20deg);
    color: #fbc531;
  }
  67%{
    transform: rotate(-20deg);
  }
}
/* other menu animation */

.nav-button {
  text-transform: uppercase;
  position: absolute;
  right: 1rem;
  top: 0.3rem;
  z-index: 89;
}
.nav-wrapper {
  background-color: #dddddd;
  box-shadow: -1px 0px 3px 0px rgba(0, 0, 0, 0.75);
  height: 100%;
  padding-top: 80px;
  perspective: 1000px;
  position: fixed;
  right: 0;
  top: 0;
  transform: translateX(100%);
  transition: transform 0.6s, visibility 0.6s;
  visibility: hidden;
  width: 25vw;
  min-width: 10rem;
  max-width: 16rem;
  z-index: 79;
}
.nav-visible .nav-wrapper {
  transform: translateX(0);
  visibility: visible;
}
.nav {
  position: relative;
  transform: rotateY(90deg);
  transform-origin: left center;
  transition: transform 0.6s;
  padding-left: 0;
}
.nav-visible .nav {
  transform: rotateY(0deg);
}
.nav li.active a {
  background-color: #b3b3b3;
}
.nav a {
  display: block;
  line-height: 1.4;
  padding: 0.7em 0.5em 0.7em 1.4em;
  text-transform: uppercase;
}
.nav a:focus, .nav a:hover {
  background-color: #cccccc;
  text-decoration: none;
}
.nav-marker {
  background-color: currentColor;
  height: 2.8rem;
  position: absolute;
  right: 0;
  top: 80px;
  width: 3px;
}
.nav-marker:before {
  border: 5px solid transparent;
  border-right-color: inherit;
  content: "";
  height: 0;
  position: absolute;
  right: 3px;
  top: 50%;
  transform: translateY(-50%);
  width: 0;
}
.color-1 {
  color: rgb(122, 122, 122);
}
.button-lines {
  background: transparent;
  border: none;
  cursor: pointer;
  display: inline-block;
  padding: 1rem 0.5rem;
  user-select: none;
}
.lines {
  background: #fff;
  border-radius: 0.2rem;
  display: inline-block;
  height: 0.2rem;
  width: 2rem;
  position: relative;
}
.lines:hover {
  background: #000;
}
.lines:after, .lines:before {
  background: #fff;
  border-radius: 0.2rem;
  display: inline-block;
  height: 0.2rem;
  width: 2rem;
  content: "";
  left: 0;
  position: absolute;
}
.lines:after:hover, .lines:before:hover {
  background: #000;
}
.lines:after {
  top: 0.5rem;
}
.lines:before {
  top: -0.5rem;
}
.button-lines-x .lines {
  transition: background 0.3s ease;
}
.button-lines-x .lines:after, .button-lines-x .lines:before {
  transform-origin: 50% 50% 0;
  transition: top 0.3s 0.4s ease, transform 0.3s ease;
}
.nav-visible .button-lines-x .lines {
  background: transparent;
}
.nav-visible .button-lines-x .lines:after, .nav-visible .button-lines-x .lines:before {
  top: 0;
  transition: top 0.3s ease, transform 0.3s 0.3s ease;
}
.nav-visible .button-lines-x .lines:after {
  transform: rotate3d(0, 0, 1, 45deg);
}
.nav-visible .button-lines-x .lines:before {
  transform: rotate3d(0, 0, 1, -45deg);
}
li  {
  list-style: none;
}
.nav-list a{
  padding-right: 0;
  padding-left: 0;
  text-align: left;
  margin-left: 1rem;
  margin-right: 1rem;
  border-bottom: 1px solid gray;
}