.modal-wrapper * {
    padding: 0;
    margin: 0;
}
.modal-wrapper {
    width: 100vw;
    height: 100vh;
    z-index: 89;
    position: absolute;
    top: 0;
    background-color: rgba(0, 0, 0, 0.4);
}
.modal {
    background-color: white;
    position: absolute;
    width: 50%;
    min-width: 290px;
    left: 50%;
    top: 50%;
    transform: translate(-50%,-50%);
    /* height: 40%;
    top: 30%;
    left: 25%; */
    border-radius: 5px;
}
.modal-header {
    padding-top: 0.4rem;
    padding-left: 1rem;
    padding-bottom: 0.4rem;
    font-size: 1.8rem;
}
.modal-exit {
    float: right;
    text-align: right;
    font-size: 1.4rem;
    padding-right: 1rem;
    padding-top: 0.4rem;
}
.modal-exit a {
    color: black;
    text-decoration: none;
}
.modal {
    overflow: scroll;
    
}
.modal-content p {
    margin-bottom: 1rem;
    padding-left: 1rem;
    padding-right: 1rem;
}